import React, { useEffect, useState } from "react";
import Button from "react-bootstrap/Button";
import { Container } from "react-bootstrap";
import Table from "react-bootstrap/Table";
import AddStudents from "./AddStudents";
import EditStudents from "./EditStudents";
import DeleteStudents from "./DeleteStudents";

const GetStudents = () => {
  const [isModalOpen, setIsModalOpen] = useState(false);
  const [isEditOpen, setIsEditOpen] = useState(false);
  const [isDelOpen, setIsDelOpen] = useState(false);
  const [users, setUsers] = useState([]);
  const [updateId, setUpdateId] = useState('');
  useEffect(() => {
    UsersGet();
  }, []);


  const UsersGet = () => {
    fetch("https://crudcrud.com/api/ff51d21a533c464f8592058ffe9f2127/crud")
      .then((res) => res.json())
      .then((result) => {
        setUsers(result);
      });
  };
  const showModal = () => {
    setIsModalOpen(true);
  };
  const editModal = (id) => {
    setUpdateId(id)
    setIsEditOpen(true);

  };
  const deleteModal = (id) => {
    setUpdateId(id)
    setIsDelOpen(true);
  };

  return (
    <div>
      <div className="container">
        <div className="d-flex justify-content-end my-2">
          <Button onClick={showModal} className="d-flex">
            Add Records
          </Button>
        </div>
        <Table striped bordered hover>
          <thead className="text-center">
            <tr>
              <th>#</th>
              <th>Name</th>
              <th>E-Mail</th>
              <th>D-O-B</th>
              <th>Gender</th>
              <th>Subjects</th>
              <th>Specialization</th>
              <th>Sports Activity</th>
              <th>Action</th>
            </tr>
          </thead>
          <tbody className="text-center">
            {users.map((user, index) => (
              <tr key={index}>
                <th scope="row">{index + 1}</th>
                <td>{user.name}</td>
                <td>{user.email}</td>
                <td>{user.dob}</td>
                <td>{user.gender}</td>
                <td>{user.subjects}</td>
                <td>{user.specialization}</td>
                <td>{user.activity}</td>
                <td>
                  <div className="d-flex justify-content-center ">
                    <Button className="d-flex btn-info mx-2" onClick={()=>{editModal(user._id)}} href="#">
                      Edit
                    </Button>
                    <Button className="d-flex btn-danger" onClick={()=>{deleteModal(user._id)}} href="#">
                      Delete
                    </Button>
                  </div>
                </td>
              </tr>
            ))}
          </tbody>
        </Table>
      </div> 
      <AddStudents users={users} UsersGet={UsersGet} isModalOpen={isModalOpen} setIsModalOpen={setIsModalOpen}/>
      <EditStudents users={users} UsersGet={UsersGet} isEditOpen={isEditOpen} setIsEditOpen={setIsEditOpen} updateId={updateId} setUpdateId={setUpdateId} />
      <DeleteStudents UsersGet={UsersGet} isDelOpen={isDelOpen} setIsDelOpen={setIsDelOpen} updateId={updateId} setUpdateId={setUpdateId}/>
      </div>
  
  );
};

export default GetStudents;
