import React from "react";
import { Button, message, Modal } from "antd";
import { useState } from "react";
import dayjs from 'dayjs';
import customParseFormat from 'dayjs/plugin/customParseFormat';
import {
  Checkbox,
  DatePicker,
  Form,
  Input,
  Radio,
  Select,
  Slider,
} from "antd";
dayjs.extend(customParseFormat);

const AddStudents = ({ users, setIsModalOpen, isModalOpen, UsersGet }) => {
  const [disabled, setDisabled] = useState(false);
  const [messageApi, contextHolder] = message.useMessage();
  const formRef = React.useRef(null);
  const [form, setForm] = useState({});
  const dateFormatList = ['DD/MM/YYYY', 'DD/MM/YY'];
  const setField = (field, value) => {
    setForm({
      ...form,
      [field]: value,
    });
  };
  const handleOk = () => {
    setIsModalOpen(false);
    UsersGet()
  };
  const handleCancel = () => {
    formRef.current?.resetFields();
    setIsModalOpen(false);
    UsersGet()
  };

  const [componentSize, setComponentSize] = useState("default");

  const onFormLayoutChange = ({ size }) => {
    setComponentSize(size);
  };

  const [value, setValue] = useState(1);

  function format(inputDate) {
    let date, month, year;
    date = inputDate.getDate();
    month = inputDate.getMonth() + 1;
    year = inputDate.getFullYear();
    date = date.toString().padStart(2, "0");
    month = month.toString().padStart(2, "0");
    return `${date}/${month}/${year}`;
  }

  const onFinish = (values) => {
    values.dob = format(values.dob.$d)
    values.subjects = values.subjects.join(', ');
 
    console.log("Success:", values);
    fetch("https://crudcrud.com/api/ff51d21a533c464f8592058ffe9f2127/crud", {
      method: "POST",
      headers: {
        Accept: "application/form-data",
        "Content-Type": "application/json",
      },
      body: JSON.stringify(values),
    })
      .then((res) => res.json())
      .then((result) => {
        console.log(result)
        // setForm(values)
        handleCancel()
      })
      messageApi.open({
        type: 'success',
        content: 'User Added',
        duration: 3,
        style: {
          marginTop: '10vh',
        }
      });
  };
  const onFinishFailed = (errorInfo) => {
    console.log("Failed:", errorInfo);
  };
  const onChange = () => {};

  return (
    <div>
      {contextHolder}
      <Modal
        title="Add Student"
        open={isModalOpen}
        onOk={handleOk}
        onCancel={handleCancel}
      >
        <Form
          ref={formRef}
          labelCol={{
            span: 4,
          }}
          wrapperCol={{
            span: 14,
          }}
          layout="horizontal"
          initialValues={{
            size: componentSize,
          }}
          onValuesChange={onFormLayoutChange}
          size={componentSize}
          onFinish={onFinish}
          onFinishFailed={onFinishFailed}
          autoComplete="off"
        >
          <Form.Item
            label="Name"
            name="name"
            rules={[
              {
                required: true,
                message: "Please Enter your Name!",
              },
            ]}
            hasFeedback
          >
            <Input  placeholder="Enter the Name" />
          </Form.Item>
          <Form.Item
            label="Email"
            name="email"
            rules={[
              {
                required: true,
                message: "Please Enter your Email!",
              },
              {
                type: "email",
                message : "Please Enter Valid Email"
              },
              ({ getFieldValue }) => ({
                validator(_, value) {
                  var emailFound = users.find(function (element) {
                    return element.email === value;
                  })
                  if (emailFound) {
                    return Promise.reject(new Error('This Email Id Already Exists'));
                    
                  }
                  return Promise.resolve();
                },
              }),
            ]}
            hasFeedback
          >
            <Input placeholder="Enter the Email"/>
          </Form.Item>

          <Form.Item
            label="Gender"
            name="gender"
            rules={[
              {
                required: true,
                message: "Please Select the Gender!",
              },
            ]}
            hasFeedback
          >
            <Radio.Group onChange={onChange} value={value}>
              <Radio value={"Male"}>Male</Radio>
              <Radio value={"Female"}>Female</Radio>
            </Radio.Group>
          </Form.Item>

          <Form.Item
            label="D.O.B"
            name="dob"
            rules={[
              {
                required: true,
                message: "Please Select the Date!",
              },
            ]}
            hasFeedback
          >
             <DatePicker defaultValue={dayjs('01/01/2015', dateFormatList[0])} format={dateFormatList} />
          </Form.Item>

          <Form.Item
            label="Subjects"
            name="subjects"
            value={value}
            valuePropName="checked"
            rules={[
              {
                required: true,
                message: "Please choose the Subjects!",
              },
            ]}
            hasFeedback
          >
            <Checkbox.Group
              style={{
                width: "100%",
              }}
              onChange={onChange}
            >
              <Checkbox value="Maths" >Maths</Checkbox>
              <Checkbox value="Science">Science</Checkbox>
              <Checkbox value="English ">English</Checkbox>
            </Checkbox.Group>
          </Form.Item>

          <Form.Item
            label="Specialization"
            name="specialization"
            rules={[
              {
                required: true,
                message: "Please Select the Specialization!",
              },
            ]}
            hasFeedback
          >
            <Select placeholder="Select a Specialization" value={value}>
              <Select.Option value="BCA">BCA</Select.Option>
              <Select.Option value="BBA">BBA</Select.Option>
              <Select.Option value="Bsc IT">Bsc IT</Select.Option>
              <Select.Option value="Bcom">Bcom</Select.Option>
              <Select.Option value="BA">BA</Select.Option>
            </Select>
          </Form.Item>

          <Form.Item
            label="Interest in Sports"
            name="activity"
            rules={[
              {
                required: true,
                message: "Please Select the range!",
              },
            ]}
            hasFeedback
          >
            <Slider defaultValue={0} value={value} disabled={disabled} />
          </Form.Item>

          <div className="modFut">
            <Button onClick={handleCancel}>Close</Button>
            <Button type="primary" htmlType="submit">
              Submit
            </Button>
          </div>
        </Form>
      </Modal>
    </div>
  );
};

export default AddStudents;
