import {
  Button,
  Checkbox,
  Form,
  Input,
  Modal,
  Radio,
  message,
  Select,
  Slider,
} from "antd";

import React, { useEffect, useState } from "react";
const EditStudents = ({
  setIsEditOpen,
  isEditOpen,
  UsersGet,
  users,
  updateId,
  setUpdateId,
}) => {
  const [disabled, setDisabled] = useState(false);
  const [forme, setForme] = useState("");
  const [myForm] = Form.useForm();
  const [messageApi, contextHolder] = message.useMessage();

  const setField = (field, value) => {
    setForme({
      ...forme,
      [field]: value,
    });
  };

  function dateShow(date) {
    return date.split("/").reverse().join("-");
  }
  useEffect(() => {
    if (updateId) {
      fetch(
        `https://crudcrud.com/api/ff51d21a533c464f8592058ffe9f2127/crud/${updateId}`
      )
        .then((res) => res.json())
        .then((response) => {
          console.log(response);
          var date1 = response.dob;
          date1 = date1.split("/").reverse().join("-");
          setForme(() => {
            return { ...response, dob: dateShow(response.dob)};
          });
        });
    }
  }, [updateId]);

  const handleOk = () => {
    setIsEditOpen(false);
    UsersGet()
  };
  const handleCancel = () => {
    // formRef.current?.resetFields();
    setIsEditOpen(false);
    setUpdateId("");
    UsersGet()
  };

  const [componentSize, setComponentSize] = useState("default");

  const onFormLayoutChange = ({ size }) => {
    setComponentSize(size);
  };

  function format(inputDate) {
    let date, month, year;
    date = inputDate.getDate();
    month = inputDate.getMonth() + 1;
    year = inputDate.getFullYear();
    date = date.toString().padStart(2, "0");
    month = month.toString().padStart(2, "0");
    return `${date}/${month}/${year}`;
  } 


  const onChange = ()=> {
  
  }
  const onFinish = () => {

    var x = forme?.subjects
    // if(typeof(forme?.subjects)=="object" && forme?.subjects.length>1){
    //   x = forme?.subjects.join(", ")
    // }else{
    //   console.log("first")

    // }
    x = (typeof(forme?.subjects)=="object" && forme?.subjects.length>1) ? forme?.subjects.join(", ") : forme?.subjects
    console.log(typeof(forme?.subjects))
    
    var datas = {
      name: forme?.name,
      email: forme?.email,
      gender: forme?.gender,
      dob: format(new Date(forme?.dob)),
      subjects: x,
      specialization: forme?.specialization,
      activity: forme?.activity,
    };

    console.log(typeof (forme?.subjects))
    fetch(
      `https://crudcrud.com/api/ff51d21a533c464f8592058ffe9f2127/crud/${updateId}`,
      {
        method: "PUT",
        headers: {
          Accept: "application/form-data",
          "Content-Type": "application/json",
        },
        body: JSON.stringify(datas),
      }
    )
      .then((res) => res.json())
      .then((result) => {
        console.log(result);
        handleCancel();
      });
    handleCancel();
    messageApi.open({
      type: 'success',
      content: 'User Updated',
      duration: 3,
      style: {
        marginTop: '10vh',
      }
    });
  };

  const onFinishFailed = (errorInfo) => {
    console.log("Failed:", errorInfo);
  };

  useEffect(() => {
    myForm.resetFields();
  }, [forme]);

  return (
    <div>
       {contextHolder}
      <Modal
        title="Edit Student"
        open={isEditOpen}
        onOk={handleOk}
        onCancel={handleCancel}
      >
        <Form
          form={myForm}
          labelCol={{
            span: 4,
          }}
          wrapperCol={{
            span: 14,
          }}
          layout="horizontal"
          id="myForm"
          onValuesChange={onFormLayoutChange}
          initialValues={forme}
          onFinish={onFinish}
          onFinishFailed={onFinishFailed}
          autoComplete="off"
        >
          <Form.Item
            label="Name"
            name="name"
            // value={forme.name}
            rules={[
              {
                required: true,
                message: "Please Enter your Name!",
              },
            ]}
            hasFeedback
          >
            <Input
              value={forme?.name}
              onChange={(e) => setField("name", e.target.value)}
              placeholder="Enter the Name"
            />
          </Form.Item>
          <Form.Item
            label="Email"
            name="email"
            rules={[
              {
                required: true,
                message: "Please Enter your Email!",
              },
              {
                type: "email",
                message: "Please Enter Valid Email",
              },
              ({ getFieldValue }) => ({
                validator(_, value) {
                  var emailFound = users.find(function (element) {
                    return element.email === value  && !(element._id === updateId);
                  })
                  if (emailFound) {
                    return Promise.reject(new Error('This Email Id Already Exists'));
                    
                  }
                  return Promise.resolve();
                },
              }),
            ]}
            hasFeedback
          >
            <Input
              value={forme?.email}
              onChange={(e) => setField("email", e.target.value)}
              placeholder="Enter the Email"
            />
          </Form.Item>

          <Form.Item
            label="Gender"
            name="gender"
            rules={[
              {
                required: true,
                message: "Please Select the Gender!",
              },
            ]}
            hasFeedback
          >
            <Radio.Group
              onChange={(e) => setField("gender", e.target.value)}
              value={forme?.gender}
            >
              <Radio value={"Male"}>Male</Radio>
              <Radio value={"Female"}>Female</Radio>
            </Radio.Group>
          </Form.Item>

          <Form.Item
            label="D.O.B"
            name="dob"
            rules={[
              {
                required: true,
                message: "Please Select the Date!",
              },
            ]}
            hasFeedback
          >
            <input type="date" id="dt"  onChange={(e) => setField("dob", e.target.value)} />
          </Form.Item>

          <Form.Item
            label="Subjects"
            name="subjects"
            rules={[
              {
                required: true,
                message: "Please Select the Date!",
              },
            ]}
            hasFeedback
          >
            <Checkbox.Group
              style={{
                width: "100%",
              }}
              value={forme?.subjects}
              onChange={(e) => setField("subjects", e)}
            >
              <Checkbox value="Maths">Maths</Checkbox>
              <Checkbox value="Science">Science</Checkbox>
              <Checkbox value="English ">English</Checkbox>
            </Checkbox.Group>
          </Form.Item>

          <Form.Item
            label="Specialization"
            name="specialization"
            rules={[
              {
                required: true,
                message: "Please Select the Specialization!",
              },
            ]}
            hasFeedback
          >
            <Select
              placeholder="Select a Specialization"
              onChange={(e) => setField("specialization", e)}
              value={forme?.specialization}
            >
              <Select.Option value="BCA">BCA</Select.Option>
              <Select.Option value="BBA">BBA</Select.Option>
              <Select.Option value="Bsc IT">Bsc IT</Select.Option>
              <Select.Option value="Bcom">Bcom</Select.Option>
              <Select.Option value="BA">BA</Select.Option>
            </Select>
          </Form.Item>

          <Form.Item
            label="Interest in Sports"
            name="activity"
            rules={[
              {
                required: true,
                message: "Please Select the range!",
              },
            ]}
            hasFeedback
          >
            <Slider
              //  defaultValue={0}
              value={forme?.activity}
              disabled={disabled}
              onChange={(e) => setField("activity", e)}
            />
          </Form.Item>

          <div className="modFut">
            <Button onClick={handleCancel}>Close</Button>
            <Button type="primary" htmlType="submit">
              Save
            </Button>
          </div>
        </Form>
      </Modal>
    </div>
  );
};

export default EditStudents;